(function(module) {
  'use strict';

  /**
   * @ngdoc directive
   * @name acqTabContent
   * @restrict E
   * @module proteus.uiComponents
   *
   * @description
   * The `acqTabContent` directive.
   *
   * @requires acqTabs
   */
  module.directive('acqTabContent', function() {
    return {
      restrict: 'E',
      scope: false,
      require: ['^acqTabs'],
      link: function(scope, iElement, iAttrs, ctrls) {
        var tabs = ctrls[0];

        iElement.empty();

        tabs.contentArea = iElement;
      }
    };
  });
})(angular.module('proteus.uiComponents'));
