(function(module) {
  'use strict';

  /**
   * @ngdoc directive
   * @name acqTab
   * @restrict E
   * @module proteus.uiComponents
   *
   * @description
   * The `acqTab` directive.
   *
   * @scope
   * @requires acqTabs
   *
   * @param {string=} [label] Optional attribute to specify a simple string as the tab label.
   * @param {expression=} [onSelect] Callback to execute when a tab is selected.
   * @param {expression=} [onDeselect] Callback to execute when a tab is deselected.
   * @param {expression=} [disabled=false] Whether the tab is disabled.
   */
  module.directive('acqTab', function($compile, $mdUtil, $mdConstant, $timeout) {
    return {
      restrict: 'E',
      scope: {
        label: '@',
        onSelect: '&',
        onDeselect: '&',
        disabled: '='
      },
      controller: 'AcqTabController',
      require: ['acqTab', '^acqTabs'],
      compile: function(element, attrs) {
        var tabLabel = element.find('acq-label');

        if (tabLabel.length) {
          tabLabel.remove();
        } else if (angular.isDefined(attrs.label)) {
          tabLabel = angular.element('<acq-label>').html(attrs.label);
        } else {
          throw new Error('A label is required on acq-tab.');
        }

        var tabContent = element.contents().remove();

        return function postLink(scope, element, attrs, ctrls) {
          var tab = ctrls[0];
          var tabs = ctrls[1];

          // Wait for tabs to have a tab-content transcluded
          $timeout(function() {
            transcludeTabContent();
            configureAria();

            tabs.add(tab);

            scope.$on('$destroy', function() {
              tabs.remove(tab);
            });

            if (!angular.isDefined(attrs.ngClick)) {
              element.on('click', defaultClickListener);
            }

            element.on('keydown', keydownListener);

            if (angular.isNumber(scope.$parent.$index)) {
              watchNgRepeatIndex();
            }

            if (angular.isDefined(attrs.active)) {
              watchActiveAttribute();
            }

            watchDisabled();
          });

          function transcludeTabContent() {
            var label = tabLabel.clone();

            element.append(label);

            $compile(label)(scope.$parent);

            tab.content = tabContent.clone();
          }

          //defaultClickListener isn't applied if the user provides an ngClick expression.
          function defaultClickListener() {
            scope.$apply(function() {
              tabs.select(tab);
            });
          }

          function keydownListener(ev) {
            switch (ev.keyCode) {
              case $mdConstant.KEY_CODE.SPACE:
              case $mdConstant.KEY_CODE.ENTER:
                // Fire the click handler to do normal selection if space is pressed
                element.triggerHandler('click');
                ev.preventDefault();

                break;
            }
          }

          // If tabItemCtrl is part of an ngRepeat, move the tabItemCtrl in our internal array
          // when its $index changes
          function watchNgRepeatIndex() {
            // The tabItemCtrl has an isolate scope, so we watch the $index on the parent.
            scope.$watch('$parent.$index', function $indexWatchAction(newIndex) {
              tabs.move(tab, newIndex);
            });
          }

          function watchActiveAttribute() {
            var unwatch = scope.$parent.$watch('!!(' + attrs.active + ')', activeWatchAction);
            scope.$on('$destroy', unwatch);

            function activeWatchAction(isActive) {
              var isSelected = tabs.getSelectedItem() === tab;

              if (isActive && !isSelected) {
                tabs.select(tab);
              } else if (!isActive && isSelected) {
                tabs.deselect(tab);
              }
            }
          }

          function watchDisabled() {
            scope.$watch(tab.isDisabled, disabledWatchAction);

            function disabledWatchAction(isDisabled) {
              element.attr('aria-disabled', isDisabled);

              var isSelected = (tabs.getSelectedItem() === tab);

              if (isSelected && isDisabled) {
                tabs.select(tabs.next() || tabs.previous());
              }
            }
          }

          function configureAria() {
            // Link together the content area and tabItemCtrl with an id
            var tabId = attrs.id || ('tab_' + $mdUtil.nextUid());

            element.attr({
              id: tabId,
              role: 'tab',
              tabIndex: -1 //this is also set on select/deselect in tabItemCtrl
            });

            // Only setup the contentContainer's aria attributes if tab content is provided
            if (tabContent.length) {
              var tabContentId = 'content_' + tabId;
              if (!element.attr('aria-controls')) {
                element.attr('aria-controls', tabContentId);
              }
              tab.contentContainer.attr({
                id: tabContentId,
                role: 'tabpanel',
                'aria-labelledby': tabId
              });
            }
          }
        };
      }
    };
  });
})(angular.module('proteus.uiComponents'));
