(function(module) {
  'use strict';

  function popoverFactory($document, $q, $rootScope, $mdCompiler, $animate, position) {

    /**
     * @ngdoc service
     * @name popover
     * @kind object
     * @module proteus.uiComponents
     *
     * @description
     * The `popover` service that handles popover creation
     */
    var popoverManager = {

      openedPopovers: [],
      /**
       * @ngdoc method
       * @name service:popover#create
       * @kind function
       *
       * @description
       * Creates a new popover.
       *
       * @param {Object} [config] The popover options.
       *
       * Object properties:
       *
       * - `triggerNode` - `{DOMElement}`
       * - `controller` - `{(string=|function()=)}`
       * - `controllerAs` - `{string=}`
       * - `resolve` - `{Object.<string, function>=}`
       * - `locals` - `{Object=}`
       * - `template` - `{string=|function()=}`
       * - `templateUrl` - `{string=|function()=}`
       * - `position` - `{string=}`
       * - `arrow` - `{Boolean=}`
       *
       * @returns {Popover} A popover instance
       */
      create: function(config) {
        // TODO: add `container` option to prevent broken CSS flow
        var options = angular.extend({}, config);
        var self = this;
        var triggerNode = options.triggerNode || $document[0].body;
        var popoverPosition = options.position || 'top';
        var scope = options.scope || $rootScope.$new(true);
        var deferred = $q.defer();
        var popoverTemplate;
        var popoverLinker;

        var popoverItem = {
          deferred: deferred,
          element: null,
          triggerNode: triggerNode,
          position: popoverPosition
        };

        /**
         * @ngdoc type
         * @name Popover
         * @kind class
         * @module proteus.uiComponents
         *
         * @description
         * A popover instance
         */
        var popoverInstance = {
          /**
           * @ngdoc method
           * @name Popover#show
           * @kind function
           * @module proteus.uiComponents
           *
           * @description
           * Show the popover instance.
           *
           * @returns {void}
           */
          show: function() {
            $mdCompiler.compile({
              controller: options.controller,
              controllerAs: options.controllerAs,
              resolve: options.resolve,
              locals: angular.extend({
                popoverInstance: popoverInstance
              }, options.locals),
              template: options.template,
              templateUrl: options.templateUrl
            }).then(function(linker) {
              // TODO: prevent this if already closed before the promise is resolved
              popoverLinker = linker;

              var elPosition;

              popoverLinker.link(scope);

              prepareTemplate(popoverItem, popoverLinker.element);

              elPosition = preparePosition(popoverItem);
              placePopover(popoverItem, elPosition);
              if (options.arrow) {
                placeArrow(popoverItem);
              }
              self.openedPopovers.push(popoverItem);

              return popoverInstance;
            });

            function prepareTemplate(instance, usrTemplate) {
              popoverTemplate = angular.element('<div class="popover fade">').addClass(popoverPosition);

              if (options.arrow) {
                popoverTemplate.append('<div class="arrow"></div>');
              }

              popoverTemplate.append(usrTemplate);

              instance.element = popoverTemplate[0];
            }

            function preparePosition(instance) {
              var popoverEl = angular.element(instance.element);
              var elemPosition;
              popoverEl.css({'position': 'absolute'});

              $animate.enter(popoverEl, triggerNode.parentNode, triggerNode);

              elemPosition = position.positionElement(
                instance.triggerNode, instance.element, instance.position, options.arrow
              );

              return elemPosition;
            }

            function placePopover(instance, position) {
              var popoverEl = angular.element(instance.element);

              popoverEl.css({'top': position.top + 'px', 'left': position.left + 'px'});

            }

            function placeArrow(instance) {
              var arrowPosition;
              arrowPosition = position.positionArrow(instance.triggerNode, instance.element, instance.position);

              var selector = '.popover.' + arrowPosition.pos + ' .arrow';
              if (arrowPosition && arrowPosition.top) {
                angular.element(instance.element.querySelector(selector)).css('top', arrowPosition.top + 'px');
              } else {
                angular.element(instance.element.querySelector(selector)).css('left', arrowPosition.left + 'px');
              }
            }
          },

          /**
           * @ngdoc method
           * @name Popover#hide
           * @kind function
           * @module proteus.uiComponents
           *
           * @description
           * Hide the popover instance.
           *
           * @returns {void}
           */
          hide: function() {
            $animate.leave(popoverItem.element);
            var index = self.openedPopovers.indexOf(popoverItem);
            self.openedPopovers.splice(index, 1);
          },

          /**
           * @ngdoc method
           * @name Popover#toggle
           * @kind function
           * @module proteus.uiComponents
           *
           * @description
           * Toggle the popover instance.
           *
           * @returns {void}
           */
          toggle: function() {
            if (self.openedPopovers.indexOf(popoverItem) === -1) {
              this.show();
            } else {
              this.hide();
            }
          }
        };

        return popoverInstance;
      }
    };

    return popoverManager;
  }

  module.factory('popover', popoverFactory);


})(angular.module('proteus.uiComponents'));
